      pageArray: '',
      currentPage: 0,
      itemsPerPage: 12
    }
  },
  beforeMount() {
    this.updatePages();
  },
  methods: {
    selectItem(item) {
      this.selectedItem = item;
      console.log(this.selectedItem.label);
    },
    setPage(num) {
      this.currentPage = num;
      this.updatePages();
      this.scrollToTop();
    },
    updatePages() {
      this.pageArray = this.itemsArray.slice(this.currentPage * this.itemsPerPage, (this.currentPage * this.itemsPerPage) + this.itemsPerPage);
      if (this.pageArray.length == 0 && this.currentPage > 0) {
        this.setPage(this.currentPage - 1);
      }
    },
    scrollToTop() {
      var container = document.querySelector(".itemcards");
      // container.scrollTop = 0;
      container.scrollTo = screenTop;
    }
  },
  computed: {
    searchResults() {
      return this.itemsArray.filter(newGood => {
        return newGood.label.indexOf(this.searchStringModel) > -1 || newGood.description.indexOf(this.searchStringModel) > -1
      })
    },
    pages() {
      const newPage = (number) => ({number})
      const output = []
      for (let i = 0; i < (this.itemsArray.length / this.itemsPerPage); i++) {
        output[i] = newPage(i);
      }
      return output
    }
  }
