import Vue from 'vue'
import Router from 'vue-router'
import Store from '@/components/Store'
import Feedback from '@/components/Feedback'
import About from '@/components/About'
import Details from '@/components/Details'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Store',
      component: Store
    },
    {
      path: '/about',
      name: 'About',
      component: About
    },
    {
      path: '/feedback',
      name: 'Feedback',
      component: Feedback
    },
    ,
    {
      path: 'details/:id',
      name: 'Details',
      component: Details
    }
  ]
})



